//   

.nav {
  position: relative;

  //   
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    min-width: 708px;
    min-height: 100px;
    padding: 0 20px 0 20px;
  }

  @media (min-width: $desktop-width) {
    @include centering;
    padding: 0;
  }
}

.nav__wrapper {
  position: fixed;
  z-index: 2;
  width: 100%;
  display: flex;
  justify-content: space-between;
  background-color: #ffffff;
  align-items: center;
  min-height: 66px;
  border-bottom: 1px solid #e6e6e6;

  @media (min-width: $tablet-width) {
    position: static;
    display: flex;
    justify-content: space-between;
    align-self: center;
    min-width: 175px;
    border-bottom: none;
  }
}

.nav__block {
  width: 100%;
  min-height: 65px;

  @media (min-width: $tablet-width) {
    width: 0;
    min-height: 0;
  }
}

.nav__logo {
  display: flex;
  align-items: center;
  margin-left: 20px;
}

.nav__img--pic {
  margin-right: 57px;

  @media (min-width: $tablet-width) {
    margin-right: 20px;
  }
}

.nav__toggle {
  border: none;
  padding: 15px;
  background-image: url("../img/icons/nav__toggle-icon.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-color: var(--bg-color-light);
  cursor: pointer;
  margin-right: 20px;

  @media (min-width: $tablet-width) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

.nav__list {
  text-align: center;
  list-style: none;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  width: 100%;

  @media (min-width: $tablet-width) {
    position: static;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-self: center;
    width: auto;
    min-width: 440px;
    background-color: none;
  }

  @media (min-width: $desktop-width) {
    min-width: 480px;
  }
}

.nav__link {
  font-family: var(--main-font);
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
  text-transform: uppercase;
  color: var(--primary-color);
  text-decoration: none;

  @media (min-width: $tablet-width) {
    font-family: var(--main-font);
    font-weight: 400;
    font-size: 18px;
    padding: 10px 0;
    margin: 0 10px;
  }

  @media (min-width: $tablet-width) {
    font-size: 18px;

    &--current {
      position: relative;

      &::after {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        height: 3px;
        background-color: var(--bright-color);
      }
    }
  }

  @media (min-width: $desktop-width) {
    font-size: 20px;
  }
}

.nav__item {
  border-bottom: 1px solid #e6e6e6;
  padding: 22px 0 24px 0;

  @media (min-width: $tablet-width) {
    border-bottom: none;
  }
}
